
.grid_ones{
    display: grid;
    border: 1px solid red;
    grid-template-columns: 35% 15% 400px auto;
    grid-gap: 1rem;
}

.grid_ones_items {
    padding: 10px;
    background-color: brown;
    border: 1px solid orange;
}





.grid_twos {
  display: grid;
  border: 1px solid red;
  grid-template-columns: 35% 15% 400px auto;
  grid-gap: 1rem;
}

.grid_twos_items {
  background-color: #eee;
  border: 1px solid orange;
  padding: 10px;
}




.grid_trys {
  display: grid;
  border: 1px solid red;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 250px;
  grid-auto-rows: minmax(250px, auto);
  grid-gap: 1rem;
}

.grid_trys_items {
  background-color: #eee;
  border: 1px solid orange;
  padding: 10px;
}

.grid_fifth {
  display: grid;
  border: 1px solid red;
  grid-template-columns: repeat(4, 200px);
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 1rem;
  justify-items: end;
  justify-items: center;
  align-items: start;
  height: 300px;
}

.grid_fifth_items {
  background-color: #CD5C5C;
  border: 1px dotted #32CD32;
  padding: 20px;
  width: 50px;
  height: 50px;
}

.grid_fifth_items:nth-child(1) {
  justify-self: end;
  align-self: center;
}

.grid_fifth_items:nth-child(2) {
  justify-self: end;
}

.grid_fifth_items:nth-child(3) {
  justify-self: center;
}

.grid_fifth_items:nth-child(4) {
  justify-self: stretch;
}

.grid_fifth_items:nth-child(5) {
  align-self: start;
}

.grid_fifth_items:nth-child(6) {
  justify-self: end;
  align-self: end;
}

.grid_fifth_items:nth-child(7) {
  justify-self: center;
  align-self: center;
}

.grid_fifth_items:nth-child(8) {
  justify-self: stretch;
  align-self: stretch;
}


.grid_fives {
  display: grid;
  border: 1px solid red;
  grid-template-columns: repeat(4, 200px);
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 0.56rem;
}

.grid_fives_items {
  background-color: #00FFFF;
  border: 1px dotted #556B2F;
  padding: 20px;
}

.grid_fives_items:nth-child(1) {
  grid-column: 1/2;
  grid-row: 1/4;
}

.grid_fives_items:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/1;
}

.grid_fives_items:nth-child(3) {
  grid-column: 3/3;
  grid-row: 1/3;
}

.grid_fives_items:nth-child(4) {
  grid-column: 4/4;
  grid-row: 1/4;
}

.grid_fives_items:nth-child(5) {
  grid-column: 2/3;
  grid-row: 2/5;
}

.grid_fives_items:nth-child(6) {
  grid-column: 1/2;
  grid-row: 4/7;
}

.grid_fives_items:nth-child(7) {
  grid-column: 2/3;
  grid-row: 5/8;
}

.grid_fives_items:nth-child(8) {
  grid-column: 3/4;
  grid-row: 3/7;
}

.grid_fives_items:nth-child(9) {
  grid-column: 4/4;
  grid-row: 4/6;
}

.grid_fives_items:nth-child(10) {
  grid-column: 1/2;
  grid-row: 7/8;
}

.grid_fives_items:nth-child(11) {
  grid-column: 3/4;
  grid-row: 7/8;
}

.grid_fives_items:nth-child(12) {
  grid-column: 4/4;
  grid-row: 6/8;
}

.grid {
  display: grid;
}

